import React from 'react';
import {Link} from 'react-router-dom';
import '../css/navbar.css';

interface Props {title: string}

const NavbarContent: React.FC<Props> = (props) => {
	return (
		<div className = "navbar-contents">
			<Link className = "navbar-text" to={props.title === "Home" ? "/" : props.title.toLowerCase()}>{props.title}</Link>
		</div>
	)
}

const Navbar: React.FC = () => {
	return (
		<nav className = "navbar">
			<NavbarContent title = "Home" />
			<NavbarContent title = "News" />
			<NavbarContent title = "Movie" />
			<NavbarContent title = "Cast" />
		</nav>
	);
}

export default Navbar;