import React from 'react';

interface DetailedNewsArticleProps{
	date: string,
	title: string,
	body: React.FC
}

const DetailedNewsArticle: React.FC<DetailedNewsArticleProps> = (props) => {
	return (
		<div className = "news-article">
			<h5 className = "article-date">{props.date}</h5>
			<h3>{props.title}</h3>
			<article className = "article-body">
				<props.body />
			</article>
		</div>
	);
}

let Bodies: [string, string, JSX.Element][] = [];
Bodies.push(["2020/10/09", "サイトを公開しました",
	<div>
		<p>文化祭サイトのプロトタイプを公開しました。コンテンツの充実に乞うご期待！</p>
		<p>なお、映画本体は 10/30 に公開される予定です。</p>
	</div>
]);

const News: React.FC = () => {
	return (
		<div className = "main-container">
			<div className = "contents">
				<h1 className = "contents-title">News</h1>
				<DetailedNewsArticle date = {Bodies[0][0]} title = {Bodies[0][1]} body = {() => Bodies[0][2]} />
			</div>
		</div>
	);
}

export default News;
export {Bodies};